export default {
	data() {
		return {
			partyAddress: '',
			managerPhone: '',
			manager: '',
			partyTitle: '',
			signUrl: '',
			date: '',
			titleContent: [{
				title: '甲方：',
				content: '成都凡果科技有限公司'
			}, {
				title: '法人：',
				content: '龚果'
			}, {
				title: '地址：',
				content: '成都市郫都区金融中心B栋11楼10号'
			}]
		}
	},
	onLoad() {
		this.getStorage()
		this.getDate()
	},
	methods: {
		getDate(){
			let date = new Date()
			let year = date.getFullYear()
			let month = date.getMonth() + 1
			let day = date.getDate()
			this.date = year + '年' + month + '月' + day + '日'
		},
		onRule() {
			uni.navigateTo({
				url: 'rule'
			})
		},
		onSign() {
			uni.navigateTo({
				url: 'sign'
			})
		},
		requestUrl() {
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/shopInfo/upload',
				data: {
					file: this.signUrl,
					openid: this.openId,
					type: 4,
					stat: 4
				},
				success: res => {
					console.log('success')
					uni.showToast({
						title: '提交成功',
					})
					setTimeout(() => {
						uni.navigateTo({
							url: 'setting-key'
						})
					}, 1000)
				}
			})
		},
		onSubmit() {
			if (this.signUrl == '') {
				uni.showToast({
					title: '请签名',
					icon: 'none'
				})
			} else {
				this.requestUrl()
			}
		},
		getStorage() {
			uni.getStorage({
				key: 'signUrl',
				success: res => {
					this.signUrl = res.data
				}
			})
			uni.getStorage({
				key: 'partyName',
				success: res => {
					this.partyTitle = res.data
				}
			})
			uni.getStorage({
				key: 'partyManager',
				success: res => {
					this.manager = res.data
				}
			})
			uni.getStorage({
				key: 'partyPhone',
				success: res => {
					this.managerPhone = res.data
				}
			})
			uni.getStorage({
				key: 'partyAddress',
				success: res => {
					this.partyAddress = res.data
				}
			})
		}
	}
}
